.fabfw-select-address-container {
    .form-row {
        label:not(.radio) {
            display: none;
        }
        
        .input-radio {
            display: none;
        }

        :checked + .radio {
            border-color: $red;
        }

        .radio {
            padding: 1em;
            border: 2px solid #eee;
            line-height: inherit;
            font-weight: inherit;

            &[for=fabfw_address_billing_id_new],
            &[for=fabfw_address_shipping_id_new] {
                padding: 0;
                border: 0;
            }
        }
    }
}